<template>
    <TopNav title="RSS" iconClass="bi bi-rss-fill">
        <template #nav-buttons>
            <button class="btn py-0 my-0 px-1 mx-1" type="button" @click="fetchAll">
                <i class="bi bi-arrow-clockwise fs-4"></i>
            </button>
        </template>
    </TopNav>
    <div class="container">
        <p v-if="items.length===0" class="fs-5 text-muted">Wow, such empty! 👀</p>
        <div v-if="items.length>0">
            <div v-for="item in items" :key="item.link" class="card mb-2">
                <img :src="item.imageUrl" class="card-img-top">
                <div class="card-body">
                    <div class="d-flex align-items-center mb-1">
                        <span class="badge text-bg-primary me-2">{{ item.feedName }}</span>
                        <span class="text-muted">{{ item.date }}</span>
                    </div>
                    <h5 class="card-title">{{ item.title }}</h5>
                    <p class="card-text">{{ item.description }}</p>
                    <a :href="item.link" target="_blank" class="btn btn-primary">Read more</a>
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
// IMPORTS
// -------
import { ref, onMounted } from 'vue';
import TopNav from './TopNav.vue';

// STATIC PROPERTIES
// -----------------
const defaultFeeds = [
    "https://www.wired.com/feed/rss",
    "https://www.theverge.com/rss/index.xml",
    "https://www.aljazeera.com/xml/rss/all.xml"
];

// REACTIVE PROPERTIES
// -------------------
const feeds = ref([]);
const items = ref([]);

// METHODS
// -------
const loadFeeds = () => {
    feeds.value = JSON.parse(localStorage.getItem("rssFeeds")) || defaultFeeds
}

const fetchRss = async (url) => {
    try {
        const response = await fetch(`https://api.allorigins.win/get?url=${encodeURIComponent(url)}`);
        const data = await response.json();
        const parser = new DOMParser();
        try {
            const xmlDoc = parser.parseFromString(data.contents, 'text/xml');
            const rssTitle = xmlDoc.querySelector('title').textContent;
            let rssItems = xmlDoc.querySelectorAll('item')
            if (rssItems.length===0) {
                rssItems = xmlDoc.querySelectorAll('entry')
            }
            console.log(rssTitle);
            rssItems.forEach(item => {
                let imageUrl = ""
                try {
                    const mediaThumbnail = item.getElementsByTagName('media:thumbnail');
                    imageUrl = mediaThumbnail.length === 0 ? "" : mediaThumbnail[0].getAttribute('url');
                    console.log(imageUrl);
                } catch (err) {
                    console.log("Failed to parse media.");
                }
                items.value.push({
                    feedName: rssTitle,
                    title: item.querySelector('title').textContent,
                    link: item.querySelector('link').textContent,
                    description: item.querySelector('description').textContent,
                    date: item.querySelector('pubDate').textContent,
                    imageUrl: imageUrl
                })
            })
        } catch (err) {
            console.log("Failed to parse XML document from " + url)
        }
    } catch (err) {
        console.log('Failed to fetch RSS reed: ' + url);
    }
}

const fetchAll = () => {
    feeds.value.forEach(feed => {
        fetchRss(feed);
    })
}

// LIFECYCLE HOOKS
// ---------------
onMounted(loadFeeds);
onMounted(fetchAll);

</script>